*
{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

.inputelement
{
 display: flex;
 justify-content: center;
 padding: 2rem;
 align-items: center;
 
}

.maininput
{
    width: 12rem;
    height: 2rem;
    font-size: 1.5rem;
    outline: none;
    color: red;
    outline-style:none;
    margin-right: 1rem;
    border: 1px solid red;
    padding: 1.3rem;
}
button
{
    
    display: inline-block;
    outline: none;
    cursor: pointer;
    font-size: 16px;
    line-height: 20px;
    font-weight: 600;
    border-radius: 8px;
    padding: 14px 24px;
    border: none;
    transition: box-shadow 0.2s ease 0s, -ms-transform 0.1s ease 0s, -webkit-transform 0.1s ease 0s, transform 0.1s ease 0s;
    background: linear-gradient(to right, rgb(230, 30, 77) 0%, rgb(227, 28, 95) 50%, rgb(215, 4, 102) 100%);
    color: #fff;

}
h4
{
    
}
img
{
    display: inline-block;
    width: 15rem;
    
}
.juice-data
{
    display: grid;
    padding: 1rem;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 2rem;
    background-color: #f1f1f1;
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 45px;
    font-size: 30px;
    margin-bottom: 2rem;
}
h5
{
    text-align: center;
    color:red;
    font-size: 2rem;
}
